.form {
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
  }
  
  .label {
    display: flex;
    flex-direction: column;
    margin: 0.5rem auto;
    align-items: center;
    font-size: large;
    text-align: left;
    padding: 0.5rem 1rem 0.5rem 1rem;
  }
  
  .formBtn {
    margin-top: 1rem;
    margin-bottom: 2rem;
    background-color: #8a8b8e;
    border: none;
    border-radius: 6px;
    color: #fff;
    flex-grow: 1;
    font-family: SFProText-Regular, Helvetica, Arial, sans-serif;
    font-size: 15px;
    height: 36px;
    line-height: 20px;
    margin-left: 8px;
    margin-right: 6px;
    min-width: 185px;
    padding: 0 16px 0 16px;
    color: white;
    cursor: pointer;
  }
  
  .formBtn:hover {
    background-color: #2196F3;
  cursor: pointer;
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08), 0px 2px 2px rgba(0, 0, 0, 0.12);
  }